import React from "react";

class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      items: [],
      isLoaded: false
    };
  }

  componentDidMount() {
    fetch("https://jsonplaceholder.typicode.com/users")
      .then((res) => res.json())
      .then((json) => {
        this.setState({
          isLoaded: true,
          items: json
        });
      });
  }

  render() {
    var { isLoaded, items } = this.state;
    if (!isLoaded) {
      return <div>Loading...</div>;
    } else {
      return (
        <div className="Home">
          <ul>
            {items.map((item) => (
              <li key={item.id}>
                {item.id} || {item.username} || {item.company.name}
              </li>
            ))}
            ;
          </ul>
        </div>
      );
    }
  }
}

export default Home;
