import React from "react";
import "./styles.css";

//import Home from "./Home";
//const Home = lazy(() => import("./Home"));

//import About from "./About";
//const About = lazy(() => import("./About"));

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      items: null
    };
  }

  async componentDidMount() {
    const url = "https://api.randomuser.me/";
    const response = await fetch(url);

    const data = await response.json();

    this.setState({
      items: data.results[0],
      loading: false
    });
  }

  render() {
    if (this.state.loading) {
      return <div>Loading....</div>;
    }

    if (!this.state.items) {
      return <div>didn't find item</div>;
    }

    return (
      <div>
        <div>{this.state.items.name.first}</div>
        <div>{this.state.items.name.last}</div>
        <div>{this.state.items.location.country}</div>
        <img src={this.state.items.picture.medium} />
      </div>
    );
  }
}

export default App;
